.gpt3__footer{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: var(--color-footer);
}

.gpt3__footer-heading{
    width: 100%;
    text-align: center;

    margin-bottom: 3rem;
}

.gpt3__footer-heading h1{
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 62px;
    line-height: 75px;
    text-align: center;
    letter-spacing: -0.04em;
}
.gpt3__footer-btn p{
    display: flex;
    justify-content: center;
    align-items: center;
    border:1px solid white;
    padding: 2rem;
    text-align: center;
    margin-bottom: 10rem;
    cursor: pointer;
}
.gpt3__footer-btn p{
  
    box-sizing: border-box;
    font-family: var(--font-family);
    font-size: 18px;
    line-height: 21px;
    color: #fff;
    word-spacing: 2px;
}

.gpt3__footer-links {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    /* flex-wrap: wrap; */
    flex-direction: row;

    width: 100%;
    text-align: left;
}

.gpt3__footer-links div{
    width: 100%;
    margin: 1rem;
}
.gpt3__footer-links_logo img{
    width: 118px;
    height: 30px;
    margin-bottom: 1rem;
}
.gpt3__footer-links_logo p{
    font-family: var(--font-family);
    font-size: 12px;
    line-height: 14px;
    color: #fff;
}
.gpt3__footer-links_div{
    display: flex;
    justify-content: flex-start;
    flex-direction: column;
}
.gpt3__footer-links_div h4{
    font-family: var(--font-family);
    font-size: 14px;
    line-height: 16px;
    color: #fff;
    margin-bottom: 1rem ;
}
.gpt3__footer-links_div p{
    font-family: var(--font-family);
    font-size: 12px;
    line-height: 14px;
    color: #fff;
    margin: 0.5rem  0;
    cursor: pointer;
}

@media screen  and (max-width: 768px){
    .gpt3__footer-links{
        flex-wrap: wrap;
    }
    .gpt3__footer-heading h1{
        font-size: 44px;
        line-height: 50px;
    }
    
}
@media screen  and (max-width: 550px){
    .gpt3__footer-heading h1{
        font-size: 34px;
        line-height: 42px;
    }
    
    .gpt3__footer-links_div{
        max-width: 1rem 0;
    }
    .gpt3__footer-heading p{
        font-size: 14px;
        line-height: 20px;
    }
}
@media screen  and (max-width: 550px){
    .gpt3__footer-heading h1{
        font-size: 27px;
        line-height: 38px;
    }}