.gpt3__whatgpt3{
    display: flex;
    flex-direction: column;

    padding: 2rem;
    background: var(--color-footer);
      /* ff 3.6+ */
      background:-moz-radial-gradient(circle at 3% 25%, rgba(0, 40, 83, 1) 0%, rgba(4, 12, 24, 1) 100%);

      /* safari 5.1+,chrome 10+ */
      background:-webkit-radial-gradient(circle at 3% 25%, rgba(0, 40, 83, 1) 0%, rgba(4, 12, 24, 1) 100%);
  
      /* opera 11.10+ */
      background:-o-radial-gradient(circle at 3% 25%, rgba(0, 40, 83, 1) 0%, rgba(4, 12, 24, 1) 100%);
  
      /* ie 10+ */
      background:-ms-radial-gradient(circle at 3% 25%, rgba(0, 40, 83, 1) 0%, rgba(4, 12, 24, 1) 100%);
  
      /* global 92%+ browsers support */
      background:radial-gradient(circle at 3% 25%, rgba(0, 40, 83, 1) 0%, rgba(4, 12, 24, 1) 100%);
  
    filter: drop-shadow(0px 4px 4px rgb(2, 17, 32));
}

.gpt3__whatgpt3-feature{
    display: flex;
}
.gpt3__whatgpt3-feature.gpt3__whatgpt3-container__feature{
    margin: 0;
}
.gpt3__whatgpt3-feature .gpt3__whatgpt3-container__feature-text{
    max-width: 700px;
}
.gpt3__whatgpt3-header{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin: 4rem 0 2rem;
}
.gpt3__whatgpt3-header h1{
    font-size:34px;
    line-height: 45px;
    font-weight: 800;
    max-width: 510px;
    font-family: var(--font-family);


}
.gpt3__whatgpt3-header p{
    font-size:26px;
    line-height: 30px;
    font-weight: 500;
    font-family: var(--font-family);
    color: var(--color-subtext);
    cursor: pointer;
}
.gpt3__whatgpt3-container {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    margin-top: 2rem;
}

/* Customizing feature component as per needs */
.gpt3__whatgpt3-container .gpt3__feature-container__feature {
    flex: 1;
    margin: 1rem;
    min-width: 210px;
    display: unset;
    flex-direction: column;
}

/* Customizing feature component as per needs */
.gpt3__whatgpt3-container .gpt3__feature-container_feature-text {
    margin-top: 0.5rem;
}

@media screen and (max-width: 850px) {
    .gpt3__whatgpt3-header {
        flex-direction: column;
        align-items: flex-start;
        margin-bottom: 0rem;
    }

    .gpt3__whatgpt3-header p {
        margin-top: 1rem;
    }
}

@media screen and (max-width: 650px) {
    /* Customizing feature component as per needs */
    .gpt3__whatgpt3-feature .gpt3__feature-container__feature {
        flex-direction: column;
    }

    /* Customizing feature component as per needs */
    .gpt3__whatgpt3-feature .gpt3__feature-container_feature-text {
        margin-top: 0.5rem;
    }

    .gpt3__whatgpt3-header h1 {
        font-size: 28px;
        line-height: 40px;
    }
}

@media screen and (max-width: 350px) {
    /* Customizing feature component as per needs */
    .gpt3__whatgpt3-container .gpt3__feature-container__feature {
        margin: 1rem 0;
        min-width: 100%;
    }
}